/** ------ Media Queries ------ **/
html {
  overflow-x: hidden;
}

body {
  font-size: 16px;
  background-color: #F5F2EE;
}

.d-block, picture {
  display: block;
}

.w-100, .wh-100 {
  width: 100%;
}

.h-100, .wh-100 {
  height: 100%;
}

img {
  vertical-align: top;
  height: auto;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

a, button, input[type=submit] {
  cursor: pointer;
  transition: all 0.3s ease;
}

main, section, header, footer, aside {
  width: 100%;
}

a {
  text-decoration: none;
}

textarea, button, input, select, option {
  outline: none;
}

video {
  display: block;
  width: 100%;
  aspect-ratio: 16/9;
}

.container {
  max-width: 67.5vw;
  margin-inline: auto;
}

h2 {
  font-weight: 400;
  font-size: 3.125vw;
}

h3 {
  font-weight: 400;
  font-size: 2.6041666667vw;
}

.text p {
  font-family: "GothamLight";
  font-size: 0.8854166667vw;
}

.primary_btn {
  max-width: 21.875vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1.25vw;
  font-family: "CanelaTextLight";
  padding-bottom: 0.3645833333vw;
  text-decoration: none;
  color: #8C6E47;
  line-height: 140%;
  border-bottom: 0.0520833333vw solid #8C6E47;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  font-family: "GothamLight";
}

.video__bg {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
}
.video__bg::before,
.video__bg video {
  position: absolute;
  width: 100%;
  height: 100%;
}
.video__bg::before {
  content: "";
  background: black;
  opacity: 0.7;
  z-index: 4;
}
.video__bg video {
  z-index: 3;
  aspect-ratio: initial;
  object-fit: cover;
}

.hero {
  min-height: 100vh;
  padding-block: 3rem;
  background: black;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 0;
}
.hero .container {
  position: relative;
  z-index: 5;
}
.hero_content h1 {
  font-family: "GoudyTitlingSemiBold";
  font-weight: 400;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.2em;
}
@media (min-width: 1540px) {
  .hero_content h1 {
    font-size: 3.3333333333vw;
    margin-block: 1.875vw 3.3333333333vw;
  }
}
.hero_content h1 span {
  display: block;
  color: #A38B6C;
}
.hero_content .breadlines {
  position: relative;
  width: fit-content;
  margin-inline: auto;
}
@media (min-width: 1540px) {
  .hero_content .breadlines {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-start;
    gap: 0.78125vw;
  }
}
@media (max-width: 1540px) {
  .hero_content .breadlines {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-start;
    gap: 12px;
  }
}
.hero_content .breadlines p {
  color: #ffffff;
  font-weight: 300;
  line-height: normal;
  leading-trim: both;
  text-edge: cap;
  text-transform: uppercase;
}
@media (min-width: 1540px) {
  .hero_content .breadlines p {
    font-size: 0.8854166667vw;
  }
}
.hero_content .breadlines::before {
  content: "";
}
.hero_content .breadlines::after {
  content: "";
}
.hero_content .breadlines::before, .hero_content .breadlines::after {
  display: block;
  background: #ffffff;
}
@media (min-width: 1540px) {
  .hero_content .breadlines::before, .hero_content .breadlines::after {
    width: 1.5625vw;
    height: 0.0520833333vw;
  }
}
@media (max-width: 1540px) {
  .hero_content .breadlines::before, .hero_content .breadlines::after {
    width: 24px;
    height: 1px;
  }
}
@media (min-width: 1540px) {
  .hero_actions {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 0.625vw;
  }
}
@media (max-width: 1540px) {
  .hero_actions {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 7px;
  }
