  }
}
@media (max-width: 1540px) {
  .hero_actions a {
    border: 1px solid #ffffff;
    font-size: 15px;
  }
}
.hero_actions a:nth-child(even) {
  color: #ffffff;
  background: transparent;
}
.hero_actions a:nth-child(odd) {
  background: #ffffff;
  color: #000000;
